import * as React from 'react';
import { Admin, DataProvider, Resource } from 'react-admin';
import { Amplify } from '@aws-amplify/core';
import { Layout, Login } from './layout';
import customRoutes from './routes';
import { awsConfig } from './configs/aws';
import forms from './forms';
import properties from './properties';
import { FORM_RESOURCE_NAME } from './forms/constants';
import { PROPERTY_RESOURCE_NAME } from './properties/constants';
import { CATEGORY_RESOURCE_NAME } from './categories/constants';
import { AuthProvider } from './authProvider';
import { OFFER_RESOURCE_NAME } from 'offers/contants';
import offers from 'offers';

Amplify.configure(awsConfig);

interface AppProps {
  dataProvider: DataProvider;
}

const App = ({ dataProvider }: AppProps) => {
  return (
    <Admin
      title="Nile Portal"
      authProvider={AuthProvider}
      dataProvider={dataProvider}
      customRoutes={customRoutes}
      loginPage={Login}
      layout={Layout}
      disableTelemetry
    >
      <Resource
        options={{ label: 'Forms' }}
        name={`${FORM_RESOURCE_NAME}`}
        {...forms}
      />
      <Resource
        options={{ label: 'Properties' }}
        name={`${PROPERTY_RESOURCE_NAME}`}
        {...properties}
      />
      <Resource name={`${CATEGORY_RESOURCE_NAME}`} />
      <Resource name={`${OFFER_RESOURCE_NAME}`} {...offers} />
    </Admin>
  );
};

export default App;
