import React, { FC } from 'react';
import { TopToolbar } from 'react-admin';
import { ButtonAction } from './ButtonAction';
import _get from 'lodash/get';
import { OFFER_STEPS } from 'configs/constants';
import {
  isPABeingCreated,
  isPABeingSigned,
  isWaitingAdvisorApproval,
  isDocumentExits,
} from 'helpers/Offer';

export const OfferActions: FC = props => {
  // use lodash _get, avoid props validated
  const resource = _get(props, 'resource');
  const offer = _get(props, 'data');
  const offerId = _get(offer, 'id');

  return (
    <TopToolbar>
      {isWaitingAdvisorApproval(offer) && (
        <>
          <ButtonAction
            text="Approve"
            resource={resource}
            payload={{
              id: `${offerId}/steps`,
              data: { new_step: OFFER_STEPS.awaitingSellerAction },
            }}
          />
          <ButtonAction
            text="Request Resubmission"
            resource={resource}
            payload={{
              id: `${offerId}/steps`,
              data: { new_step: OFFER_STEPS.pleaseResubmit },
            }}
          />
          <ButtonAction
            text="Decline"
            resource={resource}
            payload={{
              id: `${offerId}/steps`,
              data: { new_step: OFFER_STEPS.advisorDecline },
            }}
          />
        </>
      )}
      {isPABeingSigned(offer) && isDocumentExits(offer) && (
        <ButtonAction
          text="PA Signed"
          resource={resource}
          payload={{
            id: `${offerId}/steps`,
            data: { new_step: OFFER_STEPS.confirmTransfer },
          }}
        />
      )}
      {isPABeingCreated(offer) && (
        <ButtonAction
          text="PA Created"
          resource={resource}
          payload={{
            id: `${offerId}/steps`,
            data: { new_step: OFFER_STEPS.paBeingSigned },
          }}
        />
      )}
    </TopToolbar>
  );
};
