import React, { FC } from 'react';
import {
  Edit,
  EditProps,
  SimpleForm,
  TextField,
  Toolbar,
  SaveButton,
  FileInput,
  FileField,
  useNotify,
  SaveButtonProps,
  useDataProvider,
  FormDataConsumer,
  required,
  useRefresh,
} from 'react-admin';
import { useCallback } from 'react';
import { OfferActions } from './OfferActions';
import { UPDATE_FIELD_NAME, FILE_UPLOAD_LIMIT } from './contants';
import {
  generateFiles,
  mappingS3UrlWithFile,
  generateOfferUploadUrl,
  convertFileToServerData,
  uploadFilesToS3,
  updateData,
} from 'helpers/S3';
import _get from 'lodash/get';
import { OFFER_STEPS } from 'configs/constants';
import { DynamicTextField } from 'fields/DynamicTextField';
import { getOfferPrice, getSellers } from 'helpers/Offer';

const SaveOffer = (props: SaveButtonProps) => {
  const notify = useNotify();
  const dataProvider = useDataProvider();
  const refresh = useRefresh();

  const handleSave = useCallback(
    async values => {
      try {
        const { SignedPurchaseAgreement, id } = values;
        const planFields = generateFiles(SignedPurchaseAgreement);
        const uploadUrl = generateOfferUploadUrl(id);
        const files = convertFileToServerData(planFields);
        const urls = await updateData(uploadUrl, files, UPDATE_FIELD_NAME);
        const s3Files = mappingS3UrlWithFile(urls, planFields);
        await uploadFilesToS3(s3Files);
        notify('File uploaded');
        refresh();
      } catch (error) {
        notify(`Cannot upload files`, 'warning');
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [notify, dataProvider]
  );
  return <SaveButton {...props} onSave={handleSave} />;
};

const isShowStep200 = (currentStep: number) =>
  currentStep === OFFER_STEPS.paBeingSigned;

const isShowFileField = (currentStep: number) =>
  currentStep >= OFFER_STEPS.paBeingSigned;

const OfferEditToolbar: FC = props => {
  const currentStep = _get(props, ['record', 'CurrentStep']);
  return isShowStep200(currentStep) ? (
    <Toolbar {...props}>
      <SaveOffer submitOnEnter={true} />
    </Toolbar>
  ) : (
    <></>
  );
};

const OfferEdit: FC<EditProps> = props => {
  return (
    <Edit actions={<OfferActions />} mutationMode="optimistic" {...props}>
      <SimpleForm toolbar={<OfferEditToolbar />}>
        <TextField source="Offer.OfferNo" label="ID" />
        <TextField
          source="Property.PropertyData.FullAddress"
          label="Property Address"
        />
        <DynamicTextField label="Sellers" getValue={getSellers} />
        <DynamicTextField label="Offer Price" getValue={getOfferPrice} />
        <TextField source="Offer.Funding.PurchaseType" label="Funding" />
        <TextField source="Offer.Funding.LoanType" label="Loan Type" />
        <TextField source="Offer.Funding.DownPayment" label="Down Payment" />
        <TextField source="Step.messages.advisor" label="Status" />
        <FormDataConsumer>
          {({ formData }) => {
            const { CurrentStep } = formData;
            return (
              isShowFileField(CurrentStep) && (
                <FileInput
                  source={UPDATE_FIELD_NAME}
                  label="Signed Purchase Agreement"
                  accept="application/pdf"
                  multiple={true}
                  maxSize={FILE_UPLOAD_LIMIT}
                  validate={[required()]}
                  helperText="Please upload the signed Purchase Agreement to proceed."
                >
                  <FileField source="name" title="name" />
                </FileInput>
              )
            );
          }}
        </FormDataConsumer>
      </SimpleForm>
    </Edit>
  );
};

export default OfferEdit;
