import * as React from 'react';
import { FC, useState } from 'react';
import { useSelector } from 'react-redux';
import { MenuItemLink, MenuProps } from 'react-admin';
import { AppState } from '../types';
import { Box } from '@material-ui/core';
import SubMenu from './SubMenu';
import forms from '../forms';
import { FORM_PATH, FORM_CREATE_PATH } from '../forms/router';
import offers from 'offers';
import properties from 'properties';
import { OFFER_PATH } from 'offers/router';
import { PROPERTY_PATH } from 'properties/router';
import _get from 'lodash/get';

type MenuName = 'menuForms' | 'menuOffers' | 'menuProperties';

const Menu: FC<MenuProps> = ({ onMenuClick, logout, dense = false }) => {
  const [state, setState] = useState({
    menuForms: true,
    menuOffers: true,
    menuProperties: true,
  });
  useSelector((state: AppState) => state.theme);

  const open = useSelector((state: AppState) =>
    _get(state, 'admin.ui.sidebarOpen')
  );

  const handleToggle = (menu: MenuName) => {
    setState(state => ({ ...state, [menu]: !state[menu] }));
  };
  return (
    <Box mt={1}>
      {' '}
      <SubMenu
        handleToggle={() => handleToggle('menuForms')}
        isOpen={state.menuForms}
        sidebarIsOpen={open}
        name="Forms"
        dense={dense}
        icon={<forms.icon />}
      >
        <MenuItemLink
          to={FORM_PATH}
          primaryText="All Form"
          onClick={onMenuClick}
          sidebarIsOpen={open}
          dense={dense}
        />
        <MenuItemLink
          to={FORM_CREATE_PATH}
          primaryText="Add New"
          onClick={onMenuClick}
          sidebarIsOpen={open}
          dense={dense}
        />
      </SubMenu>
      <SubMenu
        handleToggle={() => handleToggle('menuOffers')}
        isOpen={state.menuOffers}
        sidebarIsOpen={open}
        name="Offers"
        dense={dense}
        icon={<offers.icon />}
      >
        <MenuItemLink
          to={OFFER_PATH}
          primaryText="All Offer"
          onClick={onMenuClick}
          sidebarIsOpen={open}
          dense={dense}
        />
      </SubMenu>
      <SubMenu
        handleToggle={() => handleToggle('menuProperties')}
        isOpen={state.menuProperties}
        sidebarIsOpen={open}
        name="Properties"
        dense={dense}
        icon={<properties.icon />}
      >
        <MenuItemLink
          to={PROPERTY_PATH}
          primaryText="All Properties"
          onClick={onMenuClick}
          sidebarIsOpen={open}
          dense={dense}
        />
      </SubMenu>
    </Box>
  );
};

export default Menu;
