import React from 'react';
import {
  SaveButton,
  useNotify,
  SaveButtonProps,
  useDataProvider,
  useRefresh,
} from 'react-admin';
import { useCallback } from 'react';
import {
  generateFiles,
  mappingS3UrlWithFile,
  generatePropertyUploadUrl,
  convertFileToServerData,
  uploadFilesToS3,
  updateData,
} from 'helpers/S3';
import { SERVER_FILE_TYPE } from '../constants';

export const UpdatePropertyButton = (props: SaveButtonProps) => {
  const notify = useNotify();
  const dataProvider = useDataProvider();
  const refresh = useRefresh();

  const handleSave = useCallback(
    async values => {
      try {
        const { Photos, id } = values;
        const { Thumbnail: thumbnail } = Photos;
        const planFields = generateFiles([thumbnail]);
        const uploadUrl = generatePropertyUploadUrl(id);
        const files = convertFileToServerData(planFields);
        const urls = await updateData(uploadUrl, files, SERVER_FILE_TYPE);
        const s3Files = mappingS3UrlWithFile(urls, planFields);
        await uploadFilesToS3(s3Files);
        notify('Photo uploaded');
        refresh();
      } catch (error) {
        const { message } = error;
        notify(`Cannot upload photos: ${message}`, 'warning');
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [notify, dataProvider]
  );
  return <SaveButton {...props} onSave={handleSave} />;
};
