import { v4 as uuid } from 'uuid';
import _get from 'lodash/get';
import { PROPERTY, ELEMENTS } from '../Constants';

export const setQuestionId = (question: { [x: string]: string }) => {
  question[PROPERTY.questionId] = uuid();
};

export const onCloningPanelHandler = (options: {
  elementsValue: Array<Object>;
}) => {
  const questions = _get(options, [ELEMENTS.panel, 'elementsValue']);
  questions.map(
    (question: { questionId: string }) => (question.questionId = uuid())
  );
};

export const fillEmptyQuestionId = (options: { element: any }) => {
  const element = _get(options, ['element']);
  if (element.isPage || element.isPanel || element.questionId) return;
  element[PROPERTY.questionId] = uuid();
};
