import { OFFER_STEPS } from 'configs/constants';
import _get from 'lodash/get';
import { Offer } from 'types';
import _isEmpty from 'lodash/isEmpty';
import { UPDATE_FIELD_NAME } from 'offers/contants';

export const isWaitingAdvisorApproval = (offer: Offer): boolean =>
  _get(offer, 'CurrentStep') === OFFER_STEPS.awaitingNileAdvisorApproval;

export const isPABeingSigned = (offer: Offer): boolean =>
  _get(offer, 'CurrentStep') === OFFER_STEPS.paBeingSigned;

export const isPABeingCreated = (offer: Offer): boolean =>
  _get(offer, 'CurrentStep') === OFFER_STEPS.paBeingCreated;

export const isDocumentExits = (offer: Offer): boolean =>
  !_isEmpty(_get(offer, UPDATE_FIELD_NAME));

export const getSellers = (offer: Offer): string =>
  _get(offer, ['Property', 'Owners'], []).join(', ');

export const getOfferPrice = (offer: Offer): string => {
  let price;

  if (_get(offer, ['CurrentStep']) === OFFER_STEPS.counteredBySeller)
    price = _get(offer, [
      'Offer',
      'CounterOffer',
      'SellerCounter',
      'OfferPrice',
    ]);
  else
    price = _get(offer, [
      'Offer',
      'CounterOffer',
      'BuyerCounter',
      'OfferPrice',
    ]);

  return price ? `$${price}` : '';
};
