import React from 'react';
import { FC } from 'react';
import {
  ListProps,
  List,
  Pagination,
  PaginationProps,
  Datagrid,
  TextField,
  EditButton,
} from 'react-admin';
import { ITEM_PER_PAGE } from 'configs/list';

const PropertyPagination: FC<PaginationProps> = props => (
  <Pagination {...props} />
);

const PropertyList: FC<ListProps> = props => (
  <List
    {...props}
    perPage={ITEM_PER_PAGE}
    pagination={<PropertyPagination />}
    component="div"
    actions={false}
  >
    <Datagrid>
      <TextField source="id" label="ID" />
      <TextField source="PropertyData.StreetAddress" label="Street Address" />
      <TextField source="PropertyData.County" label="County" />
      <TextField source="PropertyData.State" label="State" />
      <TextField source="PropertyData.Zip" label="Zip" />
      <TextField source="PropertyData.City" label="City" />
      <TextField source="PropertyNo" label="Property No" />
      <EditButton />
    </Datagrid>
  </List>
);

export default PropertyList;
