import * as SurveyCreator from 'survey-creator';
import * as Survey from 'survey-knockout';
import { SURVEY_CREATOR, SURVEYJS, COLOR } from '../Constants';
import '../Shared/Styles.css';
import 'survey-creator/survey-creator.css';
import '../Styles.css';
import 'survey-knockout/survey.css';
import _get from 'lodash/get';
import { nouislider, inputmask } from 'surveyjs-widgets';
import 'nouislider/distribute/nouislider.min.css';

[nouislider, inputmask].map(control => control(Survey));

const surveyStyleManager = _get(Survey, 'StylesManager');

const creatorStyleManager = _get(SurveyCreator, 'StylesManager');
const surveyTheme = _get(surveyStyleManager, `ThemeColors[${SURVEYJS.theme}]`);
const surveyCreatorTheme = _get(
  creatorStyleManager,
  `ThemeColors[${SURVEY_CREATOR.theme}]`
);

creatorStyleManager.applyTheme(SURVEY_CREATOR.theme);
surveyStyleManager.applyTheme(SURVEYJS.theme);

surveyTheme['$main-color'] = COLOR.primary;

['$primary-color', '$secondary-color', '$selection-border-color'].map(
  property => (surveyCreatorTheme[property] = COLOR.primary)
);
