import * as React from 'react';
import { useEffect, useState } from 'react';
import { SelectInput, useDataProvider } from 'react-admin';
import { useFormState } from 'react-final-form';
import _get from 'lodash/get';
import { ENDPOINT } from './constants';

const getFormTypeOptions = (formTypes: Object, CategoryId: number) => {
  const formTypesOfACategory = _get(formTypes, CategoryId, []);
  if (formTypesOfACategory === []) return [];
  return formTypesOfACategory.map((item: { id: string; name: string }) => {
    const { id, name } = item;
    return { id, name };
  });
};

const FormTypeInput = (props: any) => {
  const { values } = useFormState();
  const { CategoryId = '' } = values;
  const [formTypes, setFormTypes] = useState({});
  const dataProvider = useDataProvider();
  const [choices, setChoices] = useState([]);
  useEffect(() => {
    const fetchFormTypes = async () => {
      const { data } = await dataProvider.fetchData(ENDPOINT);
      setFormTypes(data);
    };

    fetchFormTypes();
  }, [dataProvider]);

  useEffect(() => {
    const formTypesOfACategory = getFormTypeOptions(formTypes, CategoryId);

    setChoices(formTypesOfACategory);
  }, [CategoryId, formTypes]);

  return <SelectInput choices={choices} {...props} />;
};
export default FormTypeInput;
