import * as React from 'react';
import { FC } from 'react';
import {
  Edit,
  SimpleForm,
  EditProps,
  TextInput,
  ReferenceInput,
  SelectInput,
  Record,
  Toolbar,
  SaveButton,
  required,
} from 'react-admin';
import SurveyComponent from './components/SurveyCreator';
import { CATEGORY_RESOURCE_NAME } from '../categories/constants';
import FormTypeInput from './components/FormTypeInput';

const FormEditToolbar: FC = props => (
  <Toolbar {...props}>
    <SaveButton submitOnEnter={true} />
  </Toolbar>
);

const FormEdit: FC<EditProps> = props => {
  function saveSurveyFunc() {
    // @ts-ignore
    sessionStorage.setItem('template', this.text);
  }

  const transform = (data: Record) => {
    const template = JSON.parse(sessionStorage.getItem('template') as string);
    const { Title, CategoryId, id, FormTypeId } = data;
    return { Title, CategoryId, Template: template, id, FormTypeId };
  };

  return (
    <Edit {...props} transform={transform} undoable={false}>
      <SimpleForm toolbar={<FormEditToolbar />}>
        <TextInput source="Title" validate={[required()]} />
        <ReferenceInput
          label="Category"
          source="CategoryId"
          reference={`${CATEGORY_RESOURCE_NAME}`}
        >
          <SelectInput optionText="Title" validate={[required()]} />
        </ReferenceInput>
        <FormTypeInput source="FormTypeId" validate={[required()]} />
        <SurveyComponent
          source="Template"
          name="Template"
          saveSurveyFunc={saveSurveyFunc}
        />
      </SimpleForm>
    </Edit>
  );
};

export default FormEdit;
