import * as React from 'react';
import { FC, memo } from 'react';
import PropTypes from 'prop-types';
import { TextFieldProps } from 'react-admin';
import Typography from '@material-ui/core/Typography';
import { useRecordContext } from 'ra-core';
import { sanitizeFieldRestProps } from '../utils';
import { fieldPropTypes } from '../types';

interface DynamicTextFieldProps extends TextFieldProps {
  getValue: (record: any) => string;
}

const DynamicTextField: FC<DynamicTextFieldProps> = memo<DynamicTextFieldProps>(
  props => {
    const { className, source, emptyText, getValue, ...rest } = props;
    const record = useRecordContext(props);
    const value = (getValue && getValue(record)) || '';
    return (
      <Typography
        component="span"
        variant="body2"
        className={className}
        {...sanitizeFieldRestProps(rest)}
      >
        {value != null && typeof value !== 'string'
          ? JSON.stringify(value)
          : value || emptyText}
      </Typography>
    );
  }
);

DynamicTextField.displayName = 'DynamicTextField';

DynamicTextField.defaultProps = {
  addLabel: true,
  getValue: () => '',
};

DynamicTextField.propTypes = {
  // @ts-ignore
  ...Typography.propTypes,
  ...fieldPropTypes,
  getValue: PropTypes.func,
};

export { DynamicTextField };
