import { File, NewFile, OldFile, S3Url } from '../types';
import _isEmpty from 'lodash/isEmpty';
import { v4 as uuidv4 } from 'uuid';
import { FILE_ADD_ACTION, OFFER_RESOURCE_NAME } from '../offers/contants';
import { API_ENDPOINT } from 'configs/constants';
import _forEach from 'lodash/forEach';
import dataFactory from 'dataProvider';
import { PROPERTY_RESOURCE_NAME } from '../properties/constants';

export const generateFiles = (files: Array<File>): Array<OldFile | NewFile> => {
  return files.map((file: File) => {
    const { rawFile } = file;
    if (_isEmpty(rawFile)) return { serverContent: file };
    const { name, path, size, type } = rawFile;
    const id = uuidv4();
    return {
      id,
      serverContent: {
        action: FILE_ADD_ACTION,
        name,
        file: {
          path,
        },
        id,
        size,
        type,
      },
      rawFile,
    };
  });
};

export const mappingS3UrlWithFile = (
  urls: Array<S3Url>,
  files: Array<OldFile | NewFile>
): Array<NewFile> => {
  return files.map((file: any) => {
    const url = urls.find((urlItem: S3Url) => urlItem.id === file.id);
    if (_isEmpty(url)) return false;
    return {
      ...file,
      ...url,
    };
  });
};

export const convertFileToServerData = (
  planFields: Array<OldFile | NewFile>
): Array<NewFile> => {
  return planFields.map((file: any) => {
    const { serverContent } = file;
    return serverContent;
  });
};

export const generateOfferUploadUrl = (offerId: string): string =>
  `${API_ENDPOINT}/${OFFER_RESOURCE_NAME}/${offerId}/files`;

export const generatePropertyUploadUrl = (propertyId: string): string =>
  `${API_ENDPOINT}/${PROPERTY_RESOURCE_NAME}/${propertyId}/photos`;

export const uploadFilesToS3 = async (files: Array<NewFile>) => {
  const dataProvider = await dataFactory();
  _forEach(files, (file: File) => {
    if (file) {
      const { url, rawFile } = file;
      if (url && rawFile) {
        dataProvider.uploadToS3(url, { data: rawFile });
      }
    }
  });
};

export const updateData = async (
  uploadUrl: string,
  files: Array<NewFile>,
  fileType: string
): Promise<Array<S3Url>> => {
  const dataProvider = await dataFactory();
  const { data } = await dataProvider.upload(uploadUrl, {
    data: { file_type: fileType, files },
  });
  const { urls } = data;
  return urls || data;
};
