import React, { FC } from 'react';
import { useMutation, useNotify, useRefresh } from 'react-admin';
import { Button } from '@material-ui/core';
import _get from 'lodash/get';

interface Props {
  payload: object;
  resource: string;
  text: string;
}

export const ButtonAction: FC<Props> = props => {
  const { resource, payload, text } = props;

  const notify = useNotify();
  const refresh = useRefresh();

  const [onClickHandler, { loading }] = useMutation(
    {
      type: 'update',
      resource,
      payload,
    },
    {
      onSuccess: () => {
        notify('Update success', 'info');
        refresh();
      },
      onFailure: error => {
        // Temporary fix error: response format of react-admin
        if (_get(error, ['status'])) {
          notify(`Update error: ${_get(error, ['message'])}`, 'warning');
        }
        refresh();
      },
    }
  );

  return (
    <Button color="primary" onClick={onClickHandler} disabled={loading}>
      {text}
    </Button>
  );
};
