import React, { FC } from 'react';
import {
  Edit,
  EditProps,
  SimpleForm,
  TextField,
  Toolbar,
  required,
  ImageField,
  ImageInput,
} from 'react-admin';
import { UpdatePropertyButton } from './components/UpdatePropertyButton';

import { FILE_UPLOAD_LIMIT, FILE_TYPES } from './constants';

const PropertyEditToolbar: FC = props => {
  return (
    <Toolbar {...props}>
      <UpdatePropertyButton submitOnEnter={true} />
    </Toolbar>
  );
};

const PropertyEdit: FC<EditProps> = props => {
  return (
    <Edit mutationMode="optimistic" {...props}>
      <SimpleForm toolbar={<PropertyEditToolbar />}>
        <TextField source="id" label="ID" />
        <TextField source="PropertyData.StreetAddress" label="Street Address" />
        <TextField source="PropertyData.County" label="County" />
        <TextField source="PropertyData.State" label="State" />
        <TextField source="PropertyData.Zip" label="Zip" />
        <TextField source="PropertyData.City" label="City" />
        <TextField source="PropertyNo" label="Property No" />
        <ImageInput
          validate={[required()]}
          maxSize={FILE_UPLOAD_LIMIT}
          multiple={false}
          source="Photos.Thumbnail"
          label="Photos"
          accept={FILE_TYPES}
        >
          <ImageField source="url" title="name" />
        </ImageInput>
      </SimpleForm>
    </Edit>
  );
};

export default PropertyEdit;
