import * as React from 'react';
import { FC, useEffect, useRef } from 'react';
import * as SurveyCreator from 'survey-creator';
import * as Survey from 'survey-knockout';
import { useField } from 'react-final-form';
import './setting.tsx';
import _get from 'lodash/get';
import { Widgets } from './Shared/Widgets';
import _isEmpty from 'lodash/isEmpty';
import * as CreatorHelper from './Helpers/SurveyCreator';

Widgets(Survey);

interface SurveyProps {
  source: string;
  name: string;
  saveSurveyFunc: Function;
  initialValue?: string;
}

const SurveyComponent: FC<SurveyProps> = props => {
  const { name, saveSurveyFunc, initialValue } = props;
  const {
    input: { value },
  } = useField(name);
  // @ts-ignore
  let surveyCreator: SurveyCreator = useRef({});

  useEffect(() => {
    if (_isEmpty(value) && !initialValue) return;
    if (!_isEmpty(surveyCreator.current)) return;
    const options = { showLogicTab: true, showState: true };
    surveyCreator.current = new SurveyCreator.SurveyCreator(
      'creatorElement',
      options
    );
    surveyCreator.current.showToolbox = 'left';
    surveyCreator.current.showPropertyGrid = 'right';
    surveyCreator.current.rightContainerActiveItem('toolbox');
    surveyCreator.current.isAutoSave = true;
    surveyCreator.current.showState = false;
    surveyCreator.current.saveSurveyFunc = saveSurveyFunc;
    surveyCreator.current.JSON = value;
    sessionStorage.setItem(
      'template',
      _get(surveyCreator, ['current', 'text'])
    );
    surveyCreator.current.onQuestionAdded.add((senders: any, options: any) => {
      const question = _get(options, 'question');
      CreatorHelper.setQuestionId(question);
    });
    surveyCreator.current.onPanelAdded.add(
      (senders: any, options: { elementsValue: Object[] }) => {
        CreatorHelper.onCloningPanelHandler(options);
      }
    );
    surveyCreator.current.onElementDoubleClick.add(
      (senders: any, options: { element: any }) => {
        CreatorHelper.fillEmptyQuestionId(options);
      }
    );
    surveyCreator.current.onModified.add(
      (senders: any, options: { element: any }) => {
        sessionStorage.setItem('template', _get(senders, 'text'));
      }
    );

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [value]);

  return <div id="creatorElement" />;
};

export default SurveyComponent;
