import * as React from 'react';
import { FC } from 'react';
import {
  Create,
  SimpleForm,
  CreateProps,
  TextInput,
  ReferenceInput,
  SelectInput,
  Record,
  SaveButton,
  Toolbar,
  required,
} from 'react-admin';
import SurveyComponent from './components/SurveyCreator';
import { CATEGORY_RESOURCE_NAME } from '../categories/constants';
import FormTypeInput from './components/FormTypeInput';

const FormCreateToolbar: FC = props => (
  <Toolbar {...props}>
    <SaveButton submitOnEnter={true} />
  </Toolbar>
);

const FormCreate: FC<CreateProps> = props => {
  function saveSurveyFunc() {
    // @ts-ignore
    sessionStorage.setItem('template', this.text);
  }

  const transform = (data: Record) => {
    const template = JSON.parse(sessionStorage.getItem('template') as string);
    return { ...data, Template: template };
  };

  return (
    <Create {...props} transform={transform}>
      <SimpleForm toolbar={<FormCreateToolbar />}>
        <TextInput source="Title" validate={[required()]} />
        <ReferenceInput
          label="Category"
          source="CategoryId"
          reference={`${CATEGORY_RESOURCE_NAME}`}
        >
          <SelectInput optionText="Title" validate={[required()]} />
        </ReferenceInput>
        <FormTypeInput source="FormTypeId" validate={[required()]} />
        <SurveyComponent
          source="Template"
          name="Template"
          saveSurveyFunc={saveSurveyFunc}
          initialValue="{}"
        />
      </SimpleForm>
    </Create>
  );
};

export default FormCreate;
