import * as React from 'react';
import { FC } from 'react';
import {
  List,
  Datagrid,
  TextField,
  ListProps,
  useListContext,
  TopToolbar,
  CreateButton,
  sanitizeListRestProps,
  ReferenceField,
  DateField,
  EditButton,
  Pagination,
  PaginationProps,
} from 'react-admin';
import { CATEGORY_RESOURCE_NAME } from '../categories/constants';
import { ITEM_PER_PAGE } from '../configs/list';

const FormPagination: FC<PaginationProps> = props => <Pagination {...props} />;

const ListActions: FC<ListProps> = props => {
  const { className, exporter, filters, ...rest } = props;
  const { basePath } = useListContext();
  return (
    <TopToolbar className={className} {...sanitizeListRestProps(rest)}>
      <CreateButton basePath={basePath} />
    </TopToolbar>
  );
};

const FormList: FC<ListProps> = props => (
  <List
    {...props}
    perPage={ITEM_PER_PAGE}
    pagination={<FormPagination />}
    component="div"
    sort={{ field: 'Title', order: 'DESC' }}
    actions={<ListActions />}
  >
    <Datagrid>
      <TextField source="Title" />
      <TextField source="id" />
      <ReferenceField
        label="Category"
        source="CategoryId"
        reference={`${CATEGORY_RESOURCE_NAME}`}
        link=""
      >
        <TextField source="Title" />
      </ReferenceField>
      <DateField source="CreatedDT" />
      <DateField source="UpdatedDT" />
      <TextField source="FormStatus" />
      <EditButton />
    </Datagrid>
  </List>
);

export default FormList;
